import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
export const getDeviceInfo = createAsyncThunk('@@deviceInfo/load-data', async (uid, { rejectWithValue, extra, }) => {
    try {
        const { baseUrl, deviceInfo } = extra;
        const url = `${baseUrl}${deviceInfo(uid)}`;
        const res = await fetch(url);
        if (!res.ok) {
            throw new Error(`Fetch failure with status: ${res.status}`);
        }
        const { data } = await res.json();
        return data;
    }
    catch (err) {
        return rejectWithValue(err.message);
    }
});
const initialState = {
    deviceInfo: null,
    error: undefined,
    status: 'idle',
};
const deviceInfoSlice = createSlice({
    name: '@@deviceInfo',
    reducers: {},
    initialState,
    extraReducers: (builder) => {
        builder
            .addCase(getDeviceInfo.pending, (state) => {
            state.status = 'loading';
            state.error = undefined;
        })
            .addCase(getDeviceInfo.rejected, (state, action) => {
            state.status = 'rejected';
            state.error = action.payload;
        })
            .addCase(getDeviceInfo.fulfilled, (state, action) => {
            state.status = 'received';
            state.deviceInfo = action.payload;
            state.error = undefined;
        });
    },
});
export const deviceInfoReducer = deviceInfoSlice.reducer;
export const selectDeviceId = ({ device: { deviceInfo, }, }) => deviceInfo?.device;
export const selectDeviceFetchInfo = ({ device: { error, status, }, }) => ({ error, status });
