import styled, { css, } from 'styled-components';
const applyButtonStyle = ({ variant, isUnavailable, theme, }) => {
    const grey = theme.palette.grey[40];
    const primaryGreen = theme.palette.primary[90];
    const setColor = () => isUnavailable ? grey : primaryGreen;
    const setHoverEffect = () => (isUnavailable
        && css `
        cursor: default;
        &:hover {
          box-shadow: none;
          pointer-events: none;
        }  
      `);
    return variant === 'contained'
        ? css `
        background-color: ${setColor};
        color: ${(props) => props.theme.palette.grey[0]};
        border: none;
        ${setHoverEffect};
      `
        : css `
        background-color: transparent;
        color: ${setColor};
        border: 1px solid ${setColor};
        ${setHoverEffect};
      `;
};
export const ButtonBase = styled.button `
  cursor: pointer;
  box-sizing: border-box;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;

  border-radius: ${({ theme }) => theme.shape.borderRadius.s};
  font-family: ${({ theme }) => theme.typography.fontFamily.primary};
  font-weight: ${({ theme }) => theme.typography.fontWeight[600]};
  font-size: 14px;
  padding: ${({ theme: { spacing } }) => `${spacing(2)} ${spacing(7)}`};
  transition-duration: ${({ theme }) => (theme.transitionDuration.normal)};

  &:hover {
    box-shadow: 0px 0px 10px
      ${({ theme }) => theme.palette.primary[40]};
  }

  ${applyButtonStyle}
`;
export const A = styled.a `
  display: contents;
`;
